body {
  background-color: #23a8e9;
}

#root {
  margin-bottom: 50px;
}

.empty-top-header {
  margin-top: 70px;
}

#kakao-top-header {
  position: fixed;
  width: 100%;
  border-color: black;
  border-bottom: 1px solid black;
  background-color: #ffeb00;
  height: 55px;
  top: 0;
  background-image: url('./kakao-logo.png');
  background-repeat: no-repeat;
  /* background-attachment: fixed; */
  background-position: right;
  /* background-position-y: 0; */
  background-size: 55px;
  padding: 12px;
  font-size: 1.3em;
  font-weight: bold;
  z-index: 100;
  opacity: 0.8;
}

.message-logo-image-left {
  width: 37px;
  margin-left: 15px;
  margin-bottom: 0px;
}

.message-logo-image-right {
  width: 37px;
  margin-right: 15px;
  margin-bottom: 5px;
}

.message-container {
  position: relative;
  overflow: auto;
}

.message-chatbot {
  background-color: #ffffff;
}

.message-me {
  background-color: #cf3232;
  color: #ffffff;
}

.message-friend {
  background-color: #cf3232;
  color: #ffffff;
}

.message-container .message-content {
  border-radius: 5px;
  padding: 10px;
  max-width: 70%;
}

.message-content.left {
  margin-left: 15px;
}

.message-content.right {
  margin-right: 15px;
}

.message-timeinfo-container {
  width: 25%;
}

.message-container .message-timeinfo-content {
  display: inline-block;
  bottom: 5px;
  color: #ffffff;
}

.left.message-timeinfo-container {
  text-align: left;
  margin-left: 3px;
}

.right.message-timeinfo-container {
  text-align: right;
  margin-right: 3px;
}

.left.message-timeinfo-container>.message-timeinfo-content {
  text-align: left;
}

.right.message-timeinfo-container>.message-timeinfo-content {
  text-align: right;
}

.message-container .message-content.message-content-w40 {
  width: 40%;
}

.message-container .message-content.message-content-w60 {
  width: 60%;
}

.message-content>.message-content-header {
  padding: 5px;
  text-align: center;
  border-bottom: 1px solid #ffffff;
  margin-bottom: 5px;
}

.message-content-detail a {
  color: #ffffff;
}

.message-content-detail a:link {
  color: #ffffff;
}

.message-content-detail a:visited {
  color: #ffffff;
}

.message-content-detail a:hover {
  color: #ffffff;
}

.message-content-detail a:active {
  color: #ffffff;
}

.message-content-detail-left {
  width: 40%;
  float: left;
}

.message-content-detail-right {
  width: 60%;
  float: right;
}

.chatbot-message-button {
  position: relative;
  padding: 10px;
  background: #958d2e;
  color: #ffffff;
  border: none;
  margin-top: 3px;
  margin-left: 15px;
  font-size: 1.1em;
  font-weight: bold;
  text-decoration: underline;
  max-width: 70%;
  text-align: left;
}

/* 버튼안의 라디오 */

/* The container */

.chatbot-message-button-radio {
  display: inline-block;
  /* position: relative; */
  padding-left: 35px;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */

.chatbot-message-button-radio input {
  position: absolute;
  opacity: 0;
}

/* Create a custom checkbox */

.chatbot-message-button-radio .checkmark {
  position: absolute;
  top: 3px;
  left: 3px;
  height: 25px;
  width: 25px;
  background-color: white;
  border: 2px solid black;
}

/* On mouse-over, add a grey background color */

.chatbot-message-button-radio:hover input~.checkmark {
  background-color: white;
}

/* When the checkbox is checked, add a blue background */

.chatbot-message-button-radio input:checked~.checkmark {
  background-color: white;
}

/* Create the checkmark/indicator (hidden when not checked) */

.chatbot-message-button-radio .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */

.chatbot-message-button-radio input:checked~.checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */

.chatbot-message-button-radio .checkmark:after {
  left: 7px;
  top: 4px;
  width: 5px;
  height: 10px;
  border: solid #000000;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.chatbot-message-button.padding-left-none {
  padding-left: 0px;
}

/* 검색 부분 */

.search-result-container {
  padding: 10px;
  position: relative;
}

.search-result-container .text-title {
  color: #cf3232;
  font-weight: bold;
  font-size: 1.1em;
}

.search-result-container .search-result-title {
  margin-top: 10px;
}

.search-result-container .search-result-currency {
  margin-top: 10px;
  text-align: right;
}

.search-result-sample-list {
  margin-top: 10px;
  padding-bottom: 5px;
  /* width:100%; */
  /* overflow: hidden;
    overflow-x: scroll; */
  overflow-x: auto;
  border-top: 2px solid #cf3232;
  border-bottom: 2px solid #cf3232;
  color: #c35151;
}

.search-result-sample-list ul {
  margin-top: 5px;
  margin-bottom: 5px;
  width: 1000%;
}

.search-result-sample-list li {
  float: left;
  display: block;
  width: 100px;
  border-right: 1px dashed #b26060;
  height: 100%;
  padding: 10px;
  font-weight: bold;
}

.search-result-sample-list li.active {
  background-color: #cf3232;
  color: #ffffff;
}

.select-ticket-radio {
  width: 20px;
  height: 20px;
}

.bottom-menu-list-container {
  /* position: fixed;
    bottom: 0px;
    max-height: 200px;
    width: 100%;
    background-color: white;
    z-index:100; */
  overflow: auto;
  max-height: 250px;
}

.bottom-menu-list-container li.active {
  background-color: #000000;
}

.bottom-menu-container {
  position: fixed;
  bottom: 0px;
  /* max-height: 200px; */
  width: 100%;
  background-color: white;
  z-index: 100;
  display: none;
  /* overflow: auto; */
}

.bottom-menu-container li {
  font-weight: bold;
  padding: 10px;
  text-align: center;
  border-bottom: 1px solid black;
  background-color: #cf3232;
  color: #ffffff;
}

.bottom-menu-container ul li:first-child {
  border-top: 1px solid black;
}

.bottom-menu-header {
  height: 60px;
  border-top-width: 1px;
  border-color: black;
  border-style: solid;
}

.bottom-menu-header-title {
  height: 40px;
  border-top-width: 1px;
  border-color: black;
  border-style: solid;
  font-weight: bold;
}

.bottom-menu-header button.active {
  background-color: #cf3232;
}

.chip {
  display: inline-block;
  padding: 0 10px;
  height: 35px;
  font-size: 16px;
  line-height: 35px;
  border-radius: 15px;
  background-color: #f1f1f1;
  float: left;
}

.closebtn {
  padding-left: 10px;
  color: #888;
  font-weight: bold;
  float: right;
  font-size: 20px;
  cursor: pointer;
}

.closebtn:hover {
  color: #000;
}

/* 로딩바 */
.loading-bar-container {
    position: fixed;
    display: none;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 2000;
}

.loading-bar {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
}

/* 모달 */
.modal-header-full {
  font-size: 2em;
  position: relative;
  height: 10vh;
  background-color: #ffffff;
  line-height: 10vh;
  border-bottom: 1px solid black;
  color: #ffffff;
  background-color: #cf3232;
}

.modal-header-close {
  position: absolute;
  top: 50%;
  right: 15px;
  font-size: 1.2em;
  -webkit-transform: translate(0%, -50%);
      -ms-transform: translate(0%, -50%);
          transform: translate(0%, -50%)
}

.modal-center-full {
  height: 80vh;
  background-color: #ffffff;
  line-height: 80vh;
}

.modal-footer-full {
  height: 10vh;
  background-color: #ffffff;
  line-height: 10vh;
  border-top: 1px solid black;
  color: #ffffff;
  background-color: #cf3232;
}

.bottom-global-menu {
  position: fixed;
  bottom: 0;
  margin-bottom: 5px;
  width: 100%;
  padding: 0px 5px;
}

.bottom-global-menu button {
  display: inline-block;
  float: left;
  width: 33.3%;
  padding: 7px;
  background-color: #23a8e9;
  border: none;
  color: #cf3232;
  font-size: 1.1em;
  font-weight: bold;
  border-radius: 10px;
  border: 2px solid #cf3232;
}


